@font-face {
    font-family: "Sensei";
    src: url("../font/sensei.otf") format("opentype");
}


.group_img{
    width: 100px;
    border-radius: 50%;
    float:left; 
}

.sensei{
    font-family: Sensei;
}

.block-rezult{
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: 0 auto;
}

.percent{
    color:rgba(189, 16, 16, 0.87);
    font-size: 40px;
}

.user_img{
    width: 90px;
    border-radius: 50%; 
}

.games_raund{
    min-height: 600px;
    text-align: center;
}

.img_answer{
    width: 110px;
    border-radius: 50%; 
    position: absolute;
}

.about_group{
    display: flex;
    align-items: center;
    margin: 30px auto;
    width:400px;
    margin: 0 auto;
    min-height: 100px;
    text-align: left;   
}

.wait_img{
    height: 70px;
}

.about_users{
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    align-items: center;
    margin-bottom: 30px;
}

.wait{
    justify-content: center;
    align-items: center;
    height: 400px;
    display: flex;
    
}

.party{
    width:300px;
}

.Gallery__bullet{
    /*background: red!important; */
    width: 20px;
    height: 20px;
}

.about_users button{
    margin-top: 20px;
    font-size: 25px;
    padding: 5px;
    min-width: 100px;
}

.again{
    margin-top: 20px;
    font-size: 25px;
    padding: 5px;
    min-width: 100px;
    background: black;
    color: #42a2aa;
    border: 0px;
    width: 200px;
    font-family: Sensei;
    border-radius: 5px;
}

.share{
    margin-top: 20px;
    font-size: 25px;
    padding: 5px;
    min-width: 100px;
    background: #42a2aa;
    color: #000000;
    border: 0px;
    width: 200px;
    font-family: Sensei;
    border-radius: 5px;
}

.answer_stroka{
    max-width: 300px;
    margin: 30px auto;
    display: flex;
    justify-content: space-between;
}

.first_display{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.text_play{
   float: left;
}
.text_play h1{
    
}

.play_games img{
    width:160px;
}

.button_answer{
    border:0px;
    background: none;
}